import common from '../../../common/common.js'

export default {
		data() {
			return {
				shopInfo:[],
				orderList:[],
				istuan:0,
			}
		},
		onLoad(option) {
			var that = this
			var shopInfo = uni.getStorageSync('shopInfo');
						
			
		},
		onPullDownRefresh() {
			var that = this
			uni.showNavigationBarLoading();
			console.log('下拉刷新中...');
						
			uni.hideNavigationBarLoading();
			uni.stopPullDownRefresh();
		},
		methods: {
			jumpUrl(e){
				var that = this
				var url = e.currentTarget.dataset.url
				uni.navigateTo({
					url: url
				});
			},
			//确定核销吗？
			hexiaosubmit(){
				var that = this
				uni.showModal({
					title: '提示',
					content: '确定核销订单吗？',
					success: res => {
						if(res.confirm){
							that.hexiaoOrder();
						}
					},
					fail: () => {},
					complete: () => {}
				});
			},
			//核销
			hexiaoOrder(){
				var that = this
				var orderno = that.orderno
				var shopInfo = uni.getStorageSync('shopInfo');
				uni.request({
					url: common.apiUrl + 'merchant/hexiaoOrder',
					method: 'GET',
					data: {
						shopid:shopInfo.shop_id,
						orderno:orderno,
						istuan:that.istuan
					},
					success: res => {
						if(res.data.code == 0){
							uni.showToast({
								title:'核销成功',
								icon:'success',
								duration:1500,
								success:res=>{
									setTimeout(function(){
										uni.reLaunch({
											url:'/pages/index/index'
										})
									},1500)
								}
							})
						}else{
							uni.showToast({
								title:res.data.msg,
								duration:1500
							})
						}
					},
					fail: () => {},
					complete: () => {}
				});
			},
			//扫码
			scanOrder(){
				var that = this
				uni.scanCode({
					success: function (res) {
						console.log('条码类型：' + res.scanType);
						console.log('条码内容：' + res.result);
						if(res.result){
							that.orderno = res.result
							that.getMerchantHexiaoOrderInfo();
						}
					}
				})
			},
			//获取店铺订单信息
			getMerchantHexiaoOrderInfo(){
				var that = this
				var orderno = that.orderno
				var shopInfo = uni.getStorageSync('shopInfo');
				uni.request({
					url: common.apiUrl + 'merchant/getMerchantHexiaoOrderInfo',
					method: 'GET',
					data: {
						shopid:shopInfo.shop_id,
						orderno:orderno
					},
					success: res => {
						
						if(res.data.code == 0){
							that.orderList = res.data.order_list
							that.istuan = res.data.istuan
							console.log(that.orderList);
						}else{
							uni.showToast({
								title:res.data.msg,
								icon:'none',
								duration:1500
							})
						}
						
					},
					fail: () => {},
					complete: () => {}
				});
			}
		},
	
	}
